import React, { useEffect, useRef, useState } from "react";
import * as PIXI from "pixi.js";
import "./App.css"; // Include this for modal styling

const Modal = ({ isOpen, onClose, imageUrl, metadata }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black z-50 flex items-center justify-center">
      <div className="bg-white p-6 rounded-lg max-w-4xl w-full mx-4">
        <div className="flex justify-between items-start mb-4">
          <h2 className="text-xl font-bold">
            {metadata?.title || "Image Details"}
          </h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            
          </button>
        </div>
        <img
          src={imageUrl}
          alt={metadata?.title || "Full size image"}
          className="w-full h-auto mb-4"
        />
        <div className="space-y-2">
          {metadata &&
            Object.entries(metadata).map(([key, value]) => (
              <p key={key} className="text-gray-700">
                <span className="font-semibold">{key}: </span>
                {value}
              </p>
            ))}
        </div>
      </div>
    </div>
  );
};

const InfiniteCanvas = ({ images = [] }) => {
  const containerRef = useRef(null);
  const [modalState, setModalState] = useState({
    isOpen: false,
    imageUrl: "",
    metadata: null,
  });

  useEffect(() => {
    // Initialize PIXI Application
    const app = new PIXI.Application({
      width: window.innerWidth,
      height: window.innerHeight,
      backgroundColor: 0xffffff,
      resolution: window.devicePixelRatio || 1,
      autoDensity: true,
      antialias: true,
    });

    // Add canvas to DOM
    containerRef.current.appendChild(app.view);

    // Create a container for all content
    const worldContainer = new PIXI.Container();
    app.stage.addChild(worldContainer);

    // Variables for dragging
    let isDragging = false;
    let dragStart = { x: 0, y: 0 };

    // Store the positions of placed images to avoid overlaps
    const placedImages = [];

    const isOverlapping = (x, y, width, height) => {
      return placedImages.some(
        ({ x: px, y: py, width: pWidth, height: pHeight }) =>
          x < px + pWidth &&
          x + width > px &&
          y < py + pHeight &&
          y + height > py
      );
    };

    const placeImageWithoutOverlap = (image, app) => {
      const spriteWidth = 300;
      const spriteHeight = 300;
      let x, y;
      let attempts = 0;
      const maxAttempts = 100;

      do {
        x = Math.random() * (app.screen.width * 5) - app.screen.width * 2;
        y = Math.random() * (app.screen.height * 5) - app.screen.height * 2;
        attempts++;
      } while (
        isOverlapping(x, y, spriteWidth, spriteHeight) &&
        attempts < maxAttempts
      );

      if (attempts < maxAttempts) {
        placedImages.push({ x, y, width: spriteWidth, height: spriteHeight });
        return { x, y };
      }
      return null; // Return null if placement fails after max attempts
    };

    const loadImages = () => {
      images.forEach((image) => {
        const texture = PIXI.Texture.from(image.url);
        const sprite = new PIXI.Sprite(texture);
        sprite.width = 300; // Adjust size as needed
        sprite.height = 300;
        sprite.interactive = true;
        sprite.cursor = "pointer";

        const position = placeImageWithoutOverlap(image, app);
        if (position) {
          sprite.x = position.x;
          sprite.y = position.y;

          sprite.on("pointerdown", (event) => {
            const clickStart = Date.now();
            const startPosition = {
              x: event.data.global.x,
              y: event.data.global.y,
            };

            const onPointerUp = (upEvent) => {
              const timeDiff = Date.now() - clickStart;
              const distance = Math.sqrt(
                Math.pow(upEvent.data.global.x - startPosition.x, 2) +
                  Math.pow(upEvent.data.global.y - startPosition.y, 2)
              );

              if (timeDiff < 200 && distance < 10) {
                setModalState({
                  isOpen: true,
                  imageUrl: image.url,
                  metadata: image.metadata,
                });
              }

              sprite.off("pointerup", onPointerUp);
              sprite.off("pointerupoutside", onPointerUp);
            };

            sprite.on("pointerup", onPointerUp);
            sprite.on("pointerupoutside", onPointerUp);
          });

          worldContainer.addChild(sprite);
        }
      });
    };

    // Load initial images
    loadImages();

    // Setup interaction handlers for dragging
    app.stage.interactive = true;
    app.stage.hitArea = app.screen;

    let previousPosition = { x: 0, y: 0 };

    const handlePointerDown = (event) => {
      if (!modalState.isOpen) {
        isDragging = true;
        dragStart = event.data.global.clone();
        previousPosition = { x: worldContainer.x, y: worldContainer.y };
        app.renderer.view.style.cursor = "grabbing";
      }
    };

    const handlePointerMove = (event) => {
      if (isDragging && !modalState.isOpen) {
        const newPosition = event.data.global.clone();
        worldContainer.x = previousPosition.x + (newPosition.x - dragStart.x);
        worldContainer.y = previousPosition.y + (newPosition.y - dragStart.y);
      }
    };

    const handlePointerUp = () => {
      if (!modalState.isOpen) {
        isDragging = false;
        app.renderer.view.style.cursor = "default";
      }
    };

    const handleWheel = (event) => {
      if (!modalState.isOpen) {
        event.preventDefault();
        const scaleChange = -event.deltaY * 0.001;
        const newScale = Math.min(
          Math.max(worldContainer.scale.x + scaleChange, 0.1),
          3
        );

        const mouseX = event.clientX - app.view.offsetLeft;
        const mouseY = event.clientY - app.view.offsetTop;
        const worldPos = {
          x: (mouseX - worldContainer.x) / worldContainer.scale.x,
          y: (mouseY - worldContainer.y) / worldContainer.scale.y,
        };

        worldContainer.scale.set(newScale);
        worldContainer.x = mouseX - worldPos.x * newScale;
        worldContainer.y = mouseY - worldPos.y * newScale;
      }
    };

    app.stage.on("pointerdown", handlePointerDown);
    app.stage.on("pointermove", handlePointerMove);
    app.stage.on("pointerup", handlePointerUp);
    app.stage.on("pointerupoutside", handlePointerUp);

    window.addEventListener("wheel", handleWheel, { passive: false });

    // Handle window resize
    const handleResize = () => {
      app.renderer.resize(window.innerWidth, window.innerHeight);
    };

    window.addEventListener("resize", handleResize);

    // Cleanup
    return () => {
      app.stage.off("pointerdown", handlePointerDown);
      app.stage.off("pointermove", handlePointerMove);
      app.stage.off("pointerup", handlePointerUp);
      app.stage.off("pointerupoutside", handlePointerUp);
      window.removeEventListener("resize", handleResize);
      window.removeEventListener("wheel", handleWheel);
      app.destroy(true);
    };
  }, [images, modalState.isOpen]);

  return (
    <>
      <div ref={containerRef} className="w-full h-full" />
      <Modal
        isOpen={modalState.isOpen}
        onClose={() => setModalState((prev) => ({ ...prev, isOpen: false }))}
        imageUrl={modalState.imageUrl}
        metadata={modalState.metadata}
      />
    </>
  );
};

// Example usage
const App = () => {
  // Generate sample images with random metadata
  const sampleImages = [];
  for (let i = 0; i < 250; i++) {
    sampleImages.push({
      url: `https://picsum.photos/seed/${i}/800/600`,
      metadata: {
        artist: `Artist ${i}`,
        title: `Title ${i}`,
        date: `2024-03-${(i % 30) + 1}`,
      },
    });
  }

  return (
    <div className="w-screen h-screen">
      <InfiniteCanvas images={sampleImages} />
    </div>
  );
};

export default App;
